  height: 20px; }

.question-list {
  height: calc(100vh - 250px);
  overflow: auto;
  font-size: 1.2em;
  padding: 10px;
  border: 2px #343a40 solid;
  border-radius: 10px; }

.answer {
  padding: 5px;
  padding-left: 15px;
  cursor: pointer;
  border-radius: 10px;
  margin-bottom: 5px;
  border: 2px transparent solid; }
  .answer.selected {
    border-color: orange; }
  .answer.correct {
    background: green;
    color: #fff; }

.date-picker__backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: silver;
  opacity: 0.5;
  z-index: 3600; }

.date-picker__calendar {
  position: fixed;
  top: 25vh;
